@import url("https://fonts.googleapis.com/css2?family=Poppins:wght@200;300;400;500;600;700;800&display=swap");

*.login {
  font-weight:normal;
  margin: 0 ;
  padding: 0 ;
  box-sizing: border-box ;
    font-family: "Poppins",
    sans-serif ;
  font-weight: revert;
  font-size: 20px;
}


input.login {
  font-family: "Poppins", sans-serif ;
}

.container.login {
  position: relative ;
  width: 100% ;
  background-color: #fff ;
  min-height: 100vh ;
  overflow: hidden ;
}

.forms-container.login {
  position: absolute ;
  width: 100% ;
  height: 100% ;
  top: 0 ;
  left: 0 ;
}

.signin-signup.login {
  position: absolute ;
  top: 50% ;
  transform: translate(-50%, -50%) ;
  left: 75% ;
  width: 50% ;
  transition: 0.5s 0.1s ease-in-out ;
  display: grid ;
  grid-template-columns: 1fr ;
  z-index: 5 ;
}

form.login {
  display: flex ;
  align-items: center ;
  justify-content: center ;
  flex-direction: column ;
  padding: 0rem 5rem ;
  transition: all 0.2s 0.7s ;
  overflow: hidden ;
  grid-column: 1 / 2 ;
  grid-row: 1 / 2 ;
}

form.sign-up-form.login {
  opacity: 0 ;
  z-index: 1 ;
}

form.sign-in-form.login {
  z-index: 2 ;
}

.title.login {
  font-size: 2.2rem ;
  color: #444 ;
  margin-bottom: 10px ;
}

.input-field.login {
  max-width: 380px ;
  width: 100% ;
  background-color: #f0f0f0 ;
  margin: 10px 0 ;
  height: 50px ;
  border-radius: 55px ;
  display: grid ;
  grid-template-columns: 15% 85% ;
  padding: 0 0.4rem ;
  position: relative ;
}

.input-field.login i {
  text-align: center ;
  line-height: 50px ;
  color: #acacac ;
  transition: 0.5s ;
  font-size: 1.1rem ;
}

.input-field.login input.login {
  background: none ;
  outline: none ;
  border: none ;
  line-height: 1 ;
  font-weight: 600 ;
  font-size: 1.1rem ;
  color: #333 ;
}

.input-field.login input.login::placeholder {
  color: #aaa ;
  font-weight: 500 ;
}

.social-text.login {
  padding: 0.7rem 0 ;
  font-size: 1rem ;
}

.social-media.login {
  display: flex ;
  justify-content: center ;
}

.social-icon.login {
  height: 46px ;
  width: 46px ;
  display: flex ;
  justify-content: center ;
  align-items: center ;
  margin: 0 0.45rem ;
  color: #333 ;
  border-radius: 50% ;
  border: 1px solid #333 ;
  text-decoration: none ;
  font-size: 1.1rem ;
  transition: 0.3s ;
}

.social-icon.login:hover {
  color: #4481eb ;
  border-color: #4481eb ;
}

.btn.login {
  width: 150px ;
  background-color: #5995fd ;
  border: none ;
  outline: none ;
  height: 49px ;
  border-radius: 49px ;
  color: #fff ;
  text-transform: uppercase ;
  font-weight: 600 ;
  margin: 10px 0 ;
  cursor: pointer ;
  transition: 0.5s ;
}

.btn.login:hover {
  background-color: #4d84e2 ;
}
.panels-container.login {
  position: absolute ;
  height: 100% ;
  width: 100% ;
  top: 0 ;
  left: 0 ;
  display: grid ;
  grid-template-columns: repeat(2, 1fr) ;
}

.container.login:before {
  content: "" ;
  position: absolute ;
  height: 2000px ;
  width: 2000px ;
  top: -10% ;
  right: 48% ;
  transform: translateY(-50%) ;
  background-image: linear-gradient(-45deg, #4481eb 0%, #04befe 100%) ;
  transition: 0.8s ease-in-out ;
  border-radius: 50% ;
  z-index: 6 ;
}

.image.login {
  width: 100% ;
  transition: transform 0.6s ease-in-out ;
  transition-delay: 0.1s ;
}

.panel.login {
  display: flex ;
  flex-direction: column ;
  align-items: flex-end ;
  justify-content: space-around ;
  text-align: center ;
  z-index: 6 ;
}

.left-panel.login {
  pointer-events: all ;
  padding: 3rem 17% 2rem 12% ;
}

.right-panel.login {
  pointer-events: none ;
  padding: 3rem 12% 2rem 17% ;
}

.panel.login .content.login {
  color: #fff ;
  transition: transform 0.9s ease-in-out ;
  transition-delay: 0.2s ;
}

.panel.login h3.login {
  font-weight: 600 ;
  line-height: 1 ;
  font-size: 1.5rem ;
}

.panel.login p.login {
  font-size: 0.95rem ;
  padding: 0.7rem 0 ;
}

.btn.transparent.login {
  margin: 0 ;
  background: none ;
  border: 2px solid #fff ;
  width: 130px ;
  height: 41px ;
  font-weight: 600 ;
  font-size: 0.8rem ;
}

.right-panel.login .image.login,
.right-panel.login .content.login {
  transform: translateX(800px) ;
}

/* ANIMATION */

.container.sign-up-mode.login:before {
  transform: translate(100%, -50%) ;
  right: 52% ;
}

.container.sign-up-mode.login .left-panel.login .image.login,
.container.sign-up-mode.login .left-panel.login .content.login {
  transform: translateX(-800px) ;
}

.container.sign-up-mode.login .signin-signup.login {
  left: 25% ;
}

.container.sign-up-mode form.sign-up-form.login {
  opacity: 1 ;
  z-index: 2 ;
}

.container.sign-up-mode form.sign-in-form.login {
  opacity: 0 ;
  z-index: 1 ;
}

.container.sign-up-mode.login .right-panel.login .image.login,
.container.sign-up-mode.login .right-panel.login .content.login {
  transform: translateX(0%) ;
}

.container.sign-up-mode.login .left-panel.login {
  pointer-events: none ;
}

.container.sign-up-mode.login .right-panel.login {
  pointer-events: all ;
}

@media (max-width: 870px) {
  .container.login {
    min-height: 800px ;
    height: 100vh ;
  }
  .signin-signup.login {
    width: 100% ;
    top: 95% ;
    transform: translate(-50%, -100%) ;
    transition: 0.2s 0.1s ease-in-out ;
  }

  .signin-signup.login,
  .container.sign-up-mode.login .signin-signup.login {
    left: 50% ;
  }

  .panels-container.login {
    grid-template-columns: 1fr ;
    grid-template-rows: 1fr 2fr 1fr ;
  }

  .panel.login {
    flex-direction: row ;
    justify-content: space-around ;
    align-items: center ;
    padding: 2.5rem 8% ;
    grid-column: 1 / 2 ;
  }

  .right-panel.login {
    grid-row: 3 / 4 ;
  }

  .left-panel.login {
    grid-row: 1 / 2 ;
  }

  .image.login {
    width: 200px ;
    transition: transform 0.9s ease-in-out ;
    transition-delay: 0.2s ;
  }

  .panel.login .content.login {
    padding-right: 15% ;
    transition: transform 0.9s ease-in-out ;
    transition-delay: 0.3s ;
  }

  .panel h3.login {
    font-size: 1.2rem ;
  }

  .panel.login p.login {
    font-size: 0.7rem ;
    padding: 0.5rem 0 ;
  }

  .btn.transparent.login {
    width: 110px ;
    height: 35px ;
    font-size: 0.7rem ;
  }

  .container.login:before {
    width: 1500px ;
    height: 1500px ;
    transform: translateX(-50%) ;
    left: 30% ;
    bottom: 68% ;
    right: initial ;
    top: initial ;
    transition: 1s ease-in-out ;
  }

  .container.sign-up-mode.login:before {
    transform: translate(-50%, 100%) ;
    bottom: 32% ;
    right: initial ;
  }

  .container.sign-up-mode.login .left-panel.login .image.login,
  .container.sign-up-mode .left-panel .content {
    transform: translateY(-300px) ;
  }

  .container.sign-up-mode.login .right-panel.login .image.login,
  .container.sign-up-mode.login .right-panel.login .content.login {
    transform: translateY(0px) ;
  }

  .right-panel.login .image.login,
  .right-panel.login .content.login {
    transform: translateY(300px) ;
  }

  .container.sign-up-mode.login .signin-signup.login {
    top: 5% ;
    transform: translate(-50%, 0) ;
  }
}

@media (max-width: 570px) {
  form.login {
    padding: 0 1.5rem ;
  }

  .image.login {
    display: none ;
  }
  .panel.login .content.login {
    padding: 0.5rem 1rem ;
  }
  .container.login {
    padding: 1.5rem ;
  }

  .container.login:before {
    bottom: 72% ;
    left: 50% ;
  }

  .container.sign-up-mode.login:before {
    bottom: 28% ;
    left: 50% ;
  }
}


.wrapper.login {
  display: inline-flex ;
  background-color: #f0f0f0 ;
  height: 50px ;
  max-width: 380px ;
  width: 100% ;
  align-items: center ;
  justify-content: space-evenly ;
  border-radius: 55px ;
  margin: 10px 0 ;
}

.wrapper.login .option.login {
  background: #fff ;
  height: 100% ;
  width: 100% ;
  display: flex ;
  align-items: center ;
  justify-content: space-evenly ;
  border-radius: 5px ;
  cursor: pointer ;
  border: 2px solid lightgrey ;
  transition: all 0.3s ease ;
}

.wrapper.login .option.login .dot.login {
  height: 20px ;
  width: 20px ;
  background: #d9d9d9 ;
  border-radius: 50% ;
  position: relative ;
}

.wrapper.login .option.login .dot.login::before {
  position: absolute ;
  content: "" ;
  top: 4px ;
  left: 4px ;
  width: 12px ;
  height: 12px ;
  background: #5995fd ;
  border-radius: 50% ;
  opacity: 0 ;
  transform: scale(1.5) ;
  transition: all 0.3s ease ;
}

input.login[type="radio"] {
  position: absolute ;
  z-index: -1 ;
}

#option-1.login:checked:checked~.option-1,
#option-2.login:checked:checked~.option-2 {
  border-color: #5995fd ;
  background: #5995fd ;
}

#option-1.login:checked:checked~.option-1.login .dot.login,
#option-2.login:checked:checked~.option-2.login .dot.login {
  background: #fff ;
}

#option-1.login:checked:checked~.option-1.login .dot.login::before,
#option-2.login:checked:checked~.option-2.login .dot.login::before {
  opacity: 1 ;
  transform: scale(1) ;
}

.wrapper.login .option.login span.login {
  font-size: 20px ;
  color: #808080 ;
}

#option-1.login:checked:checked~.option-1.login span.login,
#option-2.login:checked:checked~.option-2.login span.login {
  color: #fff ;
}